import React from 'react';

export default function Input({ inputValue, handleInput, inputName }) {
  //console.log(handleInput);
  return (
    <>
      <label htmlFor="text">{inputName} </label>
      <input
        type="text"
        value={inputValue}
        onChange={handleInput}
        name={inputName}
      />
    </>
  );
}
