import React, { Component } from 'react';

export default function ListItem({ id, name, number, handleSubmit }) {
  function handlePress(e) {
    e.preventDefault();
    handleSubmit(id);
  }

  return (
    <li key={id}>
      <span>Name: {name}</span>
      <br />
      <span>Number: {number}</span>
      <br />
      <button onClick={handlePress}>Delete contact</button>
    </li>
  );
}
